import { DashboardSidebar } from "@/components/dashboard/sidebar"
import { DashboardHeader } from "@/components/dashboard/dashboard-header"
import { KPICards } from "@/components/dashboard/kpi-cards"
import { AlertsPanel } from "@/components/dashboard/alerts-panel"
import { InventoryTable } from "@/components/dashboard/inventory-table"
import { EmergencyToggle } from "@/components/dashboard/emergency-toggle"
import { StatusHeatmap } from "@/components/dashboard/status-heatmap"
import { AuthGuard } from "@/components/auth/auth-guard"

export default function AdminDashboardPage() {
  return (
    <AuthGuard>
      <div className="flex h-screen bg-background">
        <DashboardSidebar isAdmin />
        <div className="flex flex-1 flex-col overflow-hidden">
          <DashboardHeader title="Medical Authority Dashboard" subtitle="Monitor drug supply across all facilities" />
          <main className="flex-1 overflow-y-auto p-6">
            <div className="space-y-6">
              {/* KPI Cards */}
              <KPICards />

              {/* Emergency Toggle */}
              <EmergencyToggle />

              {/* Two Column Layout */}
              <div className="grid gap-6 lg:grid-cols-3">
                <div className="lg:col-span-2">
                  <InventoryTable />
                </div>
                <div className="lg:col-span-1">
                  <AlertsPanel />
                </div>
              </div>
            </div>
          </main>
        </div>
      </div>
    </AuthGuard>
  )
}
